import { useState, useEffect, useCallback, useRef } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { createClient } from '@/lib/supabase/client';
import { logger } from '@/lib/logger';
import { toast } from 'sonner';

export interface Purchase {
  id: number;
  purchase_order_number?: string;
  request_date: string;
  delivery_request_date?: string | null;
  revised_delivery_request_date?: string | null;
  progress_type: string;
  is_payment_completed: boolean;
  payment_category: string;
  payment_completed_at?: string;
  payment_completed_by_name?: string;
  currency: string;
  request_type: string;
  vendor?: {
    vendor_name: string;
    vendor_payment_schedule?: string;
  };
  vendor_name: string;
  vendor_payment_schedule?: string;
  vendor_id?: number;
  contact_id?: number;
  vendor_contacts?: {
    contact_name: string;
  } | Array<{
    contact_name: string;
  }>;
  contact_name?: string;
  requester_id: string;
  requester_name: string;
  requester_full_name: string;
  project_vendor: string;
  sales_order_number: string;
  project_item: string;
  middle_manager_status?: string;
  final_manager_status?: string;
  total_amount: number;
  is_received: boolean;
  received_at?: string;

  is_po_download?: boolean;
  is_utk_checked?: boolean;
  is_statement_received?: boolean;
  items?: any[];
  purchase_request_items?: any[];
  item_name?: string;
  specification?: string;
  quantity?: number;
  unit_price_value?: number;
  amount_value?: number;
  remark?: string;
  line_number?: number;
  link?: string;
  purchase_status?: string;
}


//     -     
const globalCache = {
  purchases: null as Purchase[] | null,
  lastFetch: 0,
  userInfo: null as any,
  CACHE_DURATION: 2 * 60 * 1000, // 2     
  
  //    -  purchase    
  sharedPurchaseData: null as any[] | null,
  sharedDataTimestamp: 0,
  SHARED_CACHE_DURATION: 10 * 60 * 1000, // 10  
  
  //    
  filteredData: new Map<string, { data: Purchase[]; timestamp: number }>(),
  FILTER_CACHE_DURATION: 2 * 60 * 1000, // 2  ( )
  
  //    (employees, vendors, contacts)
  referenceData: {
    employees: null as any[] | null,
    vendors: null as any[] | null,
    contacts: null as any[] | null,
    lastRefFetch: 0,
    REF_CACHE_DURATION: 30 * 60 * 1000 // 30  (    )
  }
};

//    
export const clearPurchaseCache = () => {
  globalCache.purchases = null;
  globalCache.userInfo = null;
  globalCache.lastFetch = 0;
  globalCache.filteredData.clear();
  //    (  )
};

//    (employees, vendors, contacts)
export const clearReferenceCache = () => {
  globalCache.referenceData.employees = null;
  globalCache.referenceData.vendors = null;
  globalCache.referenceData.contacts = null;
  globalCache.referenceData.lastRefFetch = 0;
};

//   purchase    ()
export const getSharedPurchaseData = async (): Promise<any[]> => {
  const now = Date.now();
  const cacheValid = globalCache.sharedDataTimestamp && 
    (now - globalCache.sharedDataTimestamp) < globalCache.SHARED_CACHE_DURATION;

  //     
  if (cacheValid && globalCache.sharedPurchaseData) {
    return globalCache.sharedPurchaseData;
  }

  //      
  const supabase = createClient();
  const { data, error } = await supabase
    .from('purchase_requests')
    .select(`
      *,
      purchase_request_items(*)
    `)
    .order('request_date', { ascending: false })
    .limit(300); //    

  if (error) {
    throw error;
  }

  //  
  globalCache.sharedPurchaseData = data || [];
  globalCache.sharedDataTimestamp = now;

  return data || [];
};

//    ()
export const invalidateFilterCache = (tabKey?: string) => {
  if (tabKey) {
    for (const [key] of globalCache.filteredData.entries()) {
      if (key.includes(tabKey)) {
        globalCache.filteredData.delete(key);
      }
    }
  } else {
    globalCache.filteredData.clear();
  }
};

export const usePurchaseData = () => {
  const { currentUserRoles, currentUserName, currentUserEmail, currentUserId } = useAuth();
  const [purchases, setPurchases] = useState<Purchase[]>([]);
  const [loading, setLoading] = useState(true);
  
  //      
  const [hasInitialData, setHasInitialData] = useState(false);
  
  const supabase = createClient();
  const initializationRef = useRef(false);

  //     (employees, vendors, contacts) -  
  const loadReferenceData = useCallback(async () => {
    const now = Date.now();
    const refCache = globalCache.referenceData;
    const cacheValid = refCache.lastRefFetch && 
      (now - refCache.lastRefFetch) < refCache.REF_CACHE_DURATION;

    //     
    if (cacheValid && refCache.employees && refCache.vendors && refCache.contacts) {
      return {
        employeeList: refCache.employees,
        vendorList: refCache.vendors,
        contactList: refCache.contacts
      };
    }

    try {
      //    
      const [employeesResult, vendorsResult, contactsResult] = await Promise.all([
        supabase.from('employees').select('id, name, email'),
        supabase.from('vendors').select('id, vendor_name, vendor_payment_schedule'),
        supabase.from('vendor_contacts').select('id, contact_name')
      ]);
      
      const employeeList = employeesResult.data || [];
      const vendorList = vendorsResult.data || [];
      const contactList = contactsResult.data || [];

      //  
      refCache.employees = employeeList;
      refCache.vendors = vendorList;
      refCache.contacts = contactList;
      refCache.lastRefFetch = now;

      return { employeeList, vendorList, contactList };
    } catch (error) {
      logger.error('   ', error);
      //     
      return {
        employeeList: [],
        vendorList: [],
        contactList: []
      };
    }
  }, [supabase]);

  //    ( ,  ) -  
  useEffect(() => {
    const loadInitialData = async () => {
      if (initializationRef.current) return;
      initializationRef.current = true;
      
      const now = Date.now();
      const cacheValid = globalCache.lastFetch && (now - globalCache.lastFetch) < globalCache.CACHE_DURATION;
      
      try {
        //     
        if (cacheValid && globalCache.userInfo) {
          try {
            const employeeData = globalCache.userInfo;
            if (employeeData) {
              let roles: string[] = [];
              if (employeeData.purchase_role) {
                if (Array.isArray(employeeData.purchase_role)) {
                  roles = employeeData.purchase_role.map((r: any) => String(r).trim());
                } else {
                  const roleString = String(employeeData.purchase_role);
                  roles = roleString
                    .split(',')
                    .map((r: string) => r.trim())
                    .filter((r: string) => r.length > 0);
                }
              }
              
              // Auth context is now handled by AuthProvider, no need to set individual states
            }
            return;
          } catch (error) {
            logger.error('    ', error);
            //  
            globalCache.purchases = null;
            globalCache.userInfo = null;
            globalCache.lastFetch = 0;
          }
        }
        
        //      
        const userResult = await supabase.auth.getUser();

        //     
        if (userResult.data.user && !userResult.error) {
          // email    ( )
          let employeeData = null;
          if (userResult.data.user.email) {
            const emailResult = await supabase
              .from('employees')
              .select('*')
              .eq('email', userResult.data.user.email)
              .maybeSingle();
            
            employeeData = emailResult.data;
          }
          
          if (employeeData) {
            globalCache.userInfo = employeeData;
            
            let roles: string[] = [];
            if (employeeData.purchase_role) {
              if (Array.isArray(employeeData.purchase_role)) {
                roles = employeeData.purchase_role.map((r: any) => String(r).trim());
              } else {
                const roleString = String(employeeData.purchase_role);
                roles = roleString
                  .split(',')
                  .map((r: string) => r.trim())
                  .filter((r: string) => r.length > 0);
              }
            }
            
            // Auth context is now handled by AuthProvider globally
          }
          
          globalCache.lastFetch = now;
        }
      } catch (error) {
        logger.error('   ', error);
      }
    };

    loadInitialData();
  }, []);

  //    -    
  const loadPurchases = useCallback(async (forceRefresh?: boolean, options?: { silent?: boolean }) => {
    const showSpinner = !options?.silent;

    if (showSpinner) {
      setLoading(true);
    }
    
    try {
      //  
      const now = Date.now();
      const cacheValid = globalCache.lastFetch && (now - globalCache.lastFetch) < globalCache.CACHE_DURATION;
      
      //      
      if (forceRefresh) {
        invalidateFilterCache();
        globalCache.purchases = null;
        globalCache.lastFetch = 0;
      }
      
      if (!forceRefresh && cacheValid && globalCache.purchases) {
        try {
          setPurchases(globalCache.purchases);
          if (!hasInitialData) {
            setHasInitialData(true);
          }
          if (showSpinner) {
            setLoading(false);
          }
          return;
        } catch (error) {
          logger.error('     ', error);
          //  
          globalCache.purchases = null;
          globalCache.lastFetch = 0;
          invalidateFilterCache();
        }
      }
      
      // AuthContext      
      if (!currentUserId) {
        logger.error('  ');
        if (showSpinner) {
          setLoading(false);
        }
        return;
      }

      //        
      
      //      (  )
      const data = await getSharedPurchaseData();
      
      // Purchase   200  ( )
      const limitedData = data.slice(0, 200);

      //     -   
      const { employeeList, vendorList, contactList } = await loadReferenceData();

      //   : O(n) find()  O(1) Map  
      const employeeMap = new Map(employeeList.map(emp => [emp.id, emp]));
      const vendorMap = new Map(vendorList.map(vendor => [vendor.id, vendor]));
      const contactMap = new Map(contactList.map(contact => [contact.id, contact]));

      //    (Map   )
      const processedData = (limitedData || []).map((request: any) => {
        //     (   )
        const firstItem = request.purchase_request_items?.[0] || {};
        
        // O(1) Map   
        const requesterEmployee = employeeMap.get(request.requester_id);
        const vendorInfo = vendorMap.get(request.vendor_id);
        const contactInfo = contactMap.get(request.contact_id);
        
        return {
          id: Number(request.id),
          purchase_order_number: request.purchase_order_number as string,
          request_date: request.request_date as string,
          delivery_request_date: request.delivery_request_date ?? null,
          revised_delivery_request_date: request.revised_delivery_request_date ?? null,
          progress_type: request.progress_type as string,
          payment_completed_at: request.payment_completed_at as string,
          payment_completed_by_name: request.payment_completed_by_name as string,
          payment_category: request.payment_category as string,
          currency: request.currency as string,
          request_type: request.request_type as string,
          vendor: undefined, // vendors JOIN 
          vendor_name: request.vendor_name || '', // purchase_requests  vendor_name 
          vendor_payment_schedule: vendorInfo?.vendor_payment_schedule || '', // vendors  
          vendor_contacts: [], // vendors JOIN  
          requester_id: request.requester_id as string,
          requester_name: request.requester_name as string,
          requester_full_name: requesterEmployee?.name || request.requester_name || '',
          item_name: firstItem.item_name as string || '',
          specification: firstItem.specification as string || '',
          quantity: Number(firstItem.quantity) || 0,
          unit_price_value: Number(firstItem.unit_price_value) || 0,
          amount_value: Number(firstItem.amount_value) || 0,
          remark: firstItem.remark as string || '',
          project_vendor: request.project_vendor as string,
          sales_order_number: request.sales_order_number as string,
          project_item: request.project_item as string,
          line_number: Number(firstItem.line_number) || 1,
          contact_name: contactInfo?.contact_name || '', // contact_id   
          middle_manager_status: request.middle_manager_status,
          final_manager_status: request.final_manager_status,
          is_received: !!request.is_received,
          received_at: request.received_at as string,
          is_payment_completed: !!request.is_payment_completed,
          is_utk_checked: !!request.is_utk_checked,
          is_statement_received: !!request.is_statement_received,
          is_po_download: !!request.is_po_download,
          link: firstItem.link as string | undefined,
          //     (hanslwebapp  items ) - line_number 
          items: (request.purchase_request_items || []).sort((a: any, b: any) => 
            (a.line_number || 0) - (b.line_number || 0)),
          //   
          total_amount: request.purchase_request_items?.reduce((sum: number, item: any) => 
            sum + (Number(item.amount_value) || 0), 0) || 0,
          purchase_status: request.purchase_status || 'pending'
        };
      });


      //     
      setPurchases(processedData);
      globalCache.purchases = processedData;
      globalCache.lastFetch = now;
      
      //      
      if (!hasInitialData) {
        setHasInitialData(true);
      }
    } catch (error) {
      logger.error('   ', error);
      toast.error('    .');
    } finally {
      if (showSpinner) {
        setLoading(false);
      }
    }
  }, [currentUserId, loadReferenceData]);

  const updatePurchaseOptimistic = useCallback((purchaseId: number, updater: (prev: Purchase) => Purchase) => {
    setPurchases(prev => {
      const next = prev.map(purchase => (purchase.id === purchaseId ? updater(purchase) : purchase));
      globalCache.purchases = next;
      return next;
    });
  }, []);

  useEffect(() => {
    loadPurchases();
  }, [loadPurchases]);

  return {
    purchases,
    loading,
    currentUserRoles,
    currentUserName,
    currentUserEmail,
    currentUserId,
    refreshPurchases: loadPurchases,
    updatePurchaseOptimistic
  };
};
