import ExcelJS from 'exceljs'
import { toast } from 'sonner'

//  Excel   - DashboardMain   
export const downloadPurchaseOrderExcel = async (purchase: any): Promise<boolean> => {
  try {
    // Excel  
    const workbook = new ExcelJS.Workbook()
    const worksheet = workbook.addWorksheet('')
    
    //  
    worksheet.columns = [
      { header: '', key: 'purchase_order_number', width: 20 },
      { header: '', key: 'vendor_name', width: 30 },
      { header: '', key: 'item_name', width: 40 },
      { header: '', key: 'specification', width: 30 },
      { header: '', key: 'quantity', width: 15 },
      { header: '', key: 'unit_price', width: 20 },
      { header: '', key: 'amount', width: 20 },
      { header: '', key: 'request_date', width: 15 },
      { header: '', key: 'progress_type', width: 15 }
    ]
    
    //  
    const items = purchase.purchase_request_items || []
    items.forEach((item: any) => {
      worksheet.addRow({
        purchase_order_number: purchase.purchase_order_number,
        vendor_name: purchase.vendor_name || purchase.vendors?.vendor_name || '',
        item_name: item.item_name || '',
        specification: item.specification || '',
        quantity: item.quantity || 0,
        unit_price_value: item.unit_price_value || 0,
        amount_value: item.amount_value || 0,
        request_date: purchase.request_date || '',
        progress_type: purchase.progress_type || ''
      })
    })
    
    //  
    worksheet.getRow(1).font = { bold: true }
    worksheet.getRow(1).fill = {
      type: 'pattern',
      pattern: 'solid',
      fgColor: { argb: 'FFE0E0E0' }
    }
    
    //  
    const buffer = await workbook.xlsx.writeBuffer()
    const blob = new Blob([buffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' })
    const url = window.URL.createObjectURL(blob)
    const link = document.createElement('a')
    link.href = url
    link.download = `_${purchase.purchase_order_number}_${new Date().toISOString().slice(0, 10)}.xlsx`
    link.click()
    window.URL.revokeObjectURL(url)
    
    toast.success(' .')
    return true
  } catch (error) {
    toast.error('   .')
    return false
  }
}