
import { useState, useEffect, useCallback, useMemo } from 'react'
import { useAuth } from '@/contexts/AuthContext'
import { createClient } from '@/lib/supabase/client'
import { dashboardService } from '@/services/dashboardService'
import { getSharedPurchaseData } from '@/hooks/usePurchaseData'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Dialog, DialogContent } from '@/components/ui/dialog'
import { Input } from '@/components/ui/input'
import { Clock, CheckCircle, ArrowRight, X, Package, Truck, ShoppingCart, Download, Search } from 'lucide-react'
import { downloadPurchaseOrderExcel } from '@/utils/excelDownloader'

// Import modals -       
import LazyPurchaseDetailModal from '@/components/dashboard/LazyPurchaseDetailModal'
import PurchaseStatusModal from '@/components/dashboard/PurchaseStatusModal'
import { toast } from 'sonner'
import type { DashboardData } from '@/types/purchase'
import { useNavigate } from 'react-router-dom'
import { logger } from '@/lib/logger'
import { cache } from '@/lib/cache'

export default function DashboardMain() {
  const { employee, currentUserRoles } = useAuth()
  const [data, setData] = useState<DashboardData | null>(null)
  const [loading, setLoading] = useState(true)
  const [actionLoading, setActionLoading] = useState<string | null>(null)
  const [selectedApprovalId, setSelectedApprovalId] = useState<number | null>(null)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [undownloadedOrders, setUndownloadedOrders] = useState<any[]>([])
  const [downloadingIds, setDownloadingIds] = useState<Set<string>>(new Set())
  const [selectedOrder, setSelectedOrder] = useState<any>(null)
  const [isOrderModalOpen, setIsOrderModalOpen] = useState(false)
  
  // /   
  const [selectedStatusItem, setSelectedStatusItem] = useState<any>(null)
  const [statusModalType, setStatusModalType] = useState<'purchase' | 'delivery' | 'completed' | null>(null)
  const [isStatusModalOpen, setIsStatusModalOpen] = useState(false)
  
  //  
  const [searchTerms, setSearchTerms] = useState({
    undownloaded: '',
    pending: '',
    purchase: '',
    delivery: ''
  })
  
  const navigate = useNavigate()
  const supabase = createClient()

  const loadDashboardData = useCallback(async (showLoading = true, useCache = true) => {
    try {
      if (!employee) {
        setLoading(false)
        return
      }
      
      //     -     
      const cacheKey = `dashboard_${employee.id || employee.email}`
      
      if (useCache) {
        const cachedData = cache.get<DashboardData>(cacheKey)
        if (cachedData) {
          setData(cachedData)
          setLoading(false)
          //    (  )
          setTimeout(() => {
            loadDashboardData(false, false).catch(() => {})
          }, 100)
          return
        }
      }
      
      //     -  false    
      setLoading(false)
      
      //     - UI     
      dashboardService.getDashboardData(employee as any).then(dashboardData => {
        setData(dashboardData)
        //     (5) -  
        cache.set(cacheKey, dashboardData, 5000)
        
        //      -        
        setTimeout(() => {
          getSharedPurchaseData().catch(() => {
            //    (   )
          })
        }, 500) //    0.5  
      }).catch(error => {
        logger.error('  :', error)
      })
      
      //      
      if (currentUserRoles?.includes('lead buyer') || currentUserRoles?.includes('raw_material_manager') || currentUserRoles?.includes('consumable_manager')) {
        dashboardService.getUndownloadedOrders(employee as any).then(undownloaded => {
          setUndownloadedOrders(undownloaded)
        }).catch(() => {
          //    
        })
      }
      
    } catch (error) {
      logger.error('  :', error)
      //     -  UI  
      if (employee) {
        setData({
          employee,
          stats: { pending: 0, purchase: 0, delivery: 0, completed: 0, total: 0, myRequests: 0, urgent: 0, todayActions: 0 },
          pendingApprovals: [],
          urgentRequests: [],
          myRecentRequests: [],
          quickActions: [],
          todaySummary: { approved: 0, requested: 0, received: 0 },
          myPurchaseStatus: { waitingPurchase: [], waitingDelivery: [], recentCompleted: [] }
        } as any)
      }
    }
  }, [employee, currentUserRoles])

  // employee     (  )
  useEffect(() => {
    if (employee && !data) {
      loadDashboardData()
    }
  }, [employee]) // data loadDashboardData     

  const handleQuickApprove = async (requestId: string) => {
    if (!data?.employee) {
      toast.error('    .')
      return
    }

    //   
    if (!confirm(' ?')) {
      return
    }

    setActionLoading(requestId)
    
    // Optimistic Update:  UI 
    const originalData = data
    setData(prev => {
      if (!prev) return prev
      return {
        ...prev,
        pendingApprovals: prev.pendingApprovals.filter(item => item.id !== requestId),
        stats: {
          ...prev.stats,
          pending: Math.max(0, prev.stats.pending - 1)
        }
      }
    })

    try {
      const result = await dashboardService.quickApprove(requestId, data.employee)
      
      if (result.success) {
        toast.success(' .')
        //      (UI  )
        setTimeout(() => {
          loadDashboardData(false)  // false      
        }, 1000)
      } else {
        //     
        setData(originalData)
        toast.error(result.error || '    .')
      }
    } catch (error) {
      //     
      setData(originalData)
      toast.error('    .')
    } finally {
      setActionLoading(null)
    }
  }

  const handleStatusClick = (item: any, type: 'purchase' | 'delivery' | 'completed') => {
    setSelectedStatusItem(item)
    setStatusModalType(type)
    setIsStatusModalOpen(true)
  }

  //     
  const filterItems = useCallback((items: any[], searchTerm: string) => {
    if (!searchTerm.trim()) return items
    
    const lowerSearchTerm = searchTerm.toLowerCase()
    return items.filter(item => {
      const searchableText = [
        item.purchase_order_number || '',
        item.vendor_name || '',
        (item.purchase_request_items || [])
          .map((pItem: any) => pItem.item_name || '')
          .join(' ')
      ].join(' ').toLowerCase()
      
      return searchableText.includes(lowerSearchTerm)
    })
  }, [])

  //      
  const filteredUndownloaded = useMemo(() => 
    filterItems(undownloadedOrders, searchTerms.undownloaded), 
    [undownloadedOrders, searchTerms.undownloaded, filterItems]
  )
  
  const filteredPending = useMemo(() => 
    data?.pendingApprovals ? filterItems(data.pendingApprovals, searchTerms.pending) : [], 
    [data?.pendingApprovals, searchTerms.pending, filterItems]
  )
  
  const filteredPurchase = useMemo(() => 
    data?.myPurchaseStatus?.waitingPurchase ? filterItems(data.myPurchaseStatus.waitingPurchase, searchTerms.purchase) : [], 
    [data?.myPurchaseStatus?.waitingPurchase, searchTerms.purchase, filterItems]
  )
  
  const filteredDelivery = useMemo(() => 
    data?.myPurchaseStatus?.waitingDelivery ? filterItems(data.myPurchaseStatus.waitingDelivery, searchTerms.delivery) : [], 
    [data?.myPurchaseStatus?.waitingDelivery, searchTerms.delivery, filterItems]
  )

  const handleDownloadExcel = async (purchase: any) => {
    try {
      setDownloadingIds(prev => new Set(prev).add(purchase.id))
      
      //  Excel       
      const success = await downloadPurchaseOrderExcel(purchase)
      
      if (success && currentUserRoles.includes('lead buyer')) {
        await supabase
          .from('purchase_requests')
          .update({ is_po_download: true })
          .eq('id', purchase.id)
        
        // UI 
        setUndownloadedOrders(prev => prev.filter(item => item.id !== purchase.id))
      }
    } catch (error) {
      toast.error('   .')
    } finally {
      setDownloadingIds(prev => {
        const newSet = new Set(prev)
        newSet.delete(purchase.id)
        return newSet
      })
    }
  }

  const getPriorityColor = (priority: 'high' | 'medium' | 'low') => {
    switch (priority) {
      case 'high': return 'bg-red-100 text-red-800 border-red-200'
      case 'medium': return 'bg-yellow-100 text-yellow-800 border-yellow-200'
      case 'low': return 'bg-green-100 text-green-800 border-green-200'
      default: return 'bg-gray-100 text-gray-800 border-gray-200'
    }
  }

  const getStepColor = (step: string) => {
    switch (step) {
      case 'approval': return 'bg-yellow-100 text-yellow-800'
      case 'purchase': return 'bg-blue-100 text-blue-800'
      case 'delivery': return 'bg-purple-100 text-purple-800'
      case 'completed': return 'bg-green-100 text-green-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  //       
  const showSkeleton = loading || !data;

  //  
  const DashboardSkeleton = () => (
    <div className="p-6 space-y-6">
      {/*   */}
      <div className="flex justify-between items-center">
        <div>
          <div className="h-7 bg-gray-300 rounded w-32 animate-pulse mb-2"></div>
          <div className="h-4 bg-gray-200 rounded w-48 animate-pulse"></div>
        </div>
        <div className="h-8 bg-gray-300 rounded w-24 animate-pulse"></div>
      </div>
      
      {/*    */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        {[...Array(4)].map((_, i) => (
          <Card key={i}>
            <CardContent className="p-4">
              <div className="h-4 bg-gray-200 rounded w-20 animate-pulse mb-2"></div>
              <div className="h-8 bg-gray-300 rounded w-12 animate-pulse"></div>
            </CardContent>
          </Card>
        ))}
      </div>
      
      {/*    */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {[...Array(4)].map((_, i) => (
          <Card key={i}>
            <CardHeader>
              <div className="h-6 bg-gray-300 rounded w-32 animate-pulse"></div>
            </CardHeader>
            <CardContent className="space-y-3">
              {[...Array(3)].map((_, j) => (
                <div key={j} className="h-16 bg-gray-100 rounded animate-pulse"></div>
              ))}
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  );

  // employee       
  if (!employee && !loading) {
    return (
      <div className="flex items-center justify-center h-screen bg-gray-50">
        <div className="text-center bg-white p-8 rounded-lg border border-gray-200">
          <h3 className="modal-subtitle mb-2">    </h3>
          <p className="card-subtitle">  .</p>
        </div>
      </div>
    )
  }

  //   
  if (showSkeleton) {
    return <DashboardSkeleton />
  }

  //      
  const roles = Array.isArray(data.employee?.purchase_role)
    ? (data.employee?.purchase_role as any[]).map((r: any) => String(r).trim())
    : (data.employee?.purchase_role
        ? String(data.employee.purchase_role)
            .split(',')
            .map((r: string) => r.trim())
            .filter((r: string) => r.length > 0)
        : [])

  const canSeeApprovalBox = roles.some((r: string) => ['middle_manager', 'final_approver', 'app_admin', 'raw_material_manager', 'consumable_manager'].includes(r))

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="w-full px-4 lg:px-6">
        {/*  */}
        <div className="mb-3">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="page-title"></h1>
              <p className="page-subtitle" style={{marginTop:'-2px',marginBottom:'-4px'}}>Dashboard</p>
            </div>
            <div className="flex items-center gap-2">
              <Badge variant="outline" className="badge-text">
                {new Date().toLocaleDateString('ko-KR', { 
                  month: 'long', 
                  day: 'numeric',
                  weekday: 'short'
                })}
              </Badge>
            </div>
          </div>
        </div>


        {/*    */}
        <div className="mb-2">
          <h2 className="section-title mb-2 flex items-center gap-1.5">
            <Package className="w-3.5 h-3.5 text-gray-600" />
             
          </h2>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-3">
          {/* Lead Buyer -   */}
          {(currentUserRoles.includes('lead buyer') || currentUserRoles.includes('lead buyer')) && undownloadedOrders.length > 0 && (
            <Card className="w-full col-span-1 row-span-2 border-gray-200 shadow-sm hover:shadow-md transition-shadow">
              <CardHeader className="py-3 px-4 bg-gray-50 border-b">
                <CardTitle className="section-title flex items-center justify-between">
                  <div className="flex items-center gap-2">
                    <Download className="w-4 h-4 text-orange-600" />
                    <span> </span>
                  </div>
                  <Badge className="bg-orange-100 text-orange-700 border-orange-200 px-2 py-0.5">
                    {undownloadedOrders.length}
                  </Badge>
                </CardTitle>
              </CardHeader>
              <CardContent className="p-4">
                <div className="space-y-3">
                  {/*   */}
                  <div className="relative">
                    <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
                    <Input
                      placeholder=", ,  ..."
                      value={searchTerms.undownloaded}
                      onChange={(e) => setSearchTerms(prev => ({ ...prev, undownloaded: e.target.value }))}
                      className="pl-10 h-8 text-xs"
                    />
                  </div>
                  
                  {/*   */}
                  <div className="space-y-2 h-[36rem] overflow-y-auto">
                    {filteredUndownloaded.slice(0, 10).map((item) => {
                      const items = item.purchase_request_items || []
                      const firstItem = items[0] || {}
                      const totalAmount = items.reduce((sum: number, i: any) => {
                        return sum + (Number(i.amount_value) || 0)
                      }, 0)
                      const totalQty = items.reduce((sum: number, i: any) => {
                        return sum + (Number(i.quantity) || 0)
                      }, 0)
                      const daysSince = Math.floor((Date.now() - new Date(item.created_at).getTime()) / (1000 * 60 * 60 * 24))
                      const isAdvance = item.progress_type === ''
                      
                      return (
                        <div 
                          key={item.id} 
                          className={`border rounded-lg p-2 transition-all cursor-pointer hover:shadow-sm ${
                            isAdvance ? 'bg-red-50 hover:bg-red-100 border-red-200' : 'bg-white hover:bg-gray-50 border-gray-200'
                          }`}
                          onClick={() => {
                            setSelectedOrder(item)
                            setIsOrderModalOpen(true)
                          }}
                        >
                          <div className="flex items-center justify-between gap-2">
                            <div className="flex items-center gap-2 flex-1 min-w-0">
                              <span className="card-title">
                                {item.purchase_order_number || `PO-${item.id.slice(0, 8)}`}
                              </span>
                              <span className="card-subtitle truncate">
                                {item.vendor_name || ' '}
                              </span>
                              <span className="card-description truncate">
                                {firstItem.item_name || ''} 
                                {items.length > 1 && (
                                  <span className="text-gray-400">  {items.length - 1}</span>
                                )}
                              </span>
                              {daysSince > 3 && (
                                <Badge variant="outline" className="badge-text px-1.5 py-0 flex-shrink-0">
                                  {daysSince}
                                </Badge>
                              )}
                            </div>
                            <div>
                              <Button
                                size="sm"
                                variant="outline"
                                className="h-7 px-2 badge-text border-orange-200 hover:bg-orange-50"
                                onClick={(e) => {
                                  e.stopPropagation()
                                  handleDownloadExcel(item)
                                }}
                                disabled={downloadingIds.has(item.id)}
                              >
                                {downloadingIds.has(item.id) ? (
                                  <div className="w-3 h-3 border border-orange-600 border-t-transparent rounded-full animate-spin" />
                                ) : (
                                  <>
                                    <Download className="w-3 h-3 mr-1" />
                                    
                                  </>
                                )}
                              </Button>
                            </div>
                          </div>
                        </div>
                      )
                    })}
                  </div>
                </div>
              </CardContent>
            </Card>
          )}


          {/*   (  ) */}
          {canSeeApprovalBox && (
            <Card className="w-full col-span-1 row-span-2">
              <CardHeader className="pb-2 pt-3">
                <div className="flex items-center justify-between">
                  <CardTitle className="section-title flex items-center gap-1.5">
                    <Clock className="w-3.5 h-3.5 text-orange-500" />
                     
                    {data.pendingApprovals.length > 0 && (
                      <Badge variant="destructive" className="badge-text h-4 px-1">
                        {data.pendingApprovals.length}
                      </Badge>
                    )}
                  </CardTitle>
                  {data.pendingApprovals.length > 0 && (
                    <Button
                      size="sm"
                      variant="ghost"
                      onClick={() => navigate('/purchase')}
                      className="h-6 px-2"
                    >
                      <ArrowRight className="w-3 h-3" />
                    </Button>
                  )}
                </div>
              </CardHeader>
              <CardContent className="p-3">
                {data.pendingApprovals.length === 0 ? (
                  <div className="text-center py-4 text-gray-400">
                    <CheckCircle className="w-6 h-6 mx-auto mb-1" />
                    <p className="card-description">  </p>
                  </div>
                  ) : (
                  <div className="space-y-3">
                    {/*   */}
                    <div className="relative">
                      <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
                      <Input
                        placeholder=", ,  ..."
                        value={searchTerms.pending}
                        onChange={(e) => setSearchTerms(prev => ({ ...prev, pending: e.target.value }))}
                        className="pl-10 h-8 text-xs"
                      />
                    </div>
                    
                    {/*   */}
                    <div style={{ maxHeight: '36rem', overflowY: 'auto', display: 'flex', flexDirection: 'column', gap: '0.375rem' }}>
                      {filteredPending.slice(0, 10).map((approval, index) => {
                        const items = approval.purchase_request_items || []
                        const firstItem = items[0] || {}
                        const totalAmount = approval.total_amount || items.reduce((sum: number, i: any) => sum + (Number(i.amount_value) || 0), 0)
                        const isAdvance = approval.progress_type === ''
                        
                        return (
                          <div 
                            key={`approval-${approval.id}`} 
                            className={`border rounded-lg p-2 hover:shadow-sm transition-all cursor-pointer mb-1.5 ${
                              isAdvance ? 'bg-red-50 border-red-200' : 'hover:bg-orange-50/30'
                            }`}
                            style={{ display: 'block' }}
                            onClick={(e) => {
                              if ((e.target as HTMLElement).closest('button')) return
                              setSelectedApprovalId(Number(approval.id))
                              setIsModalOpen(true)
                            }}
                          >
                            <div className="flex items-center justify-between gap-2">
                              <div className="flex items-center gap-2 flex-1 min-w-0">
                                <span className="card-title">
                                  {approval.purchase_order_number}
                                </span>
                                <span className="card-subtitle truncate">{approval.vendor_name || ''}</span>
                                <span className="card-description truncate">
                                  {firstItem.item_name || ''} {items.length > 1 && ` ${items.length - 1}`}
                                </span>
                              </div>
                              <Button
                                size="sm"
                                onClick={(e) => {
                                  e.stopPropagation()
                                  handleQuickApprove(approval.id)
                                }}
                                disabled={actionLoading === approval.id}
                                className={`h-7 px-2 text-white badge-text shrink-0 ${
                                  approval.middle_manager_status === 'approved' 
                                    ? 'bg-blue-600 hover:bg-blue-700' 
                                    : 'bg-green-600 hover:bg-green-700'
                                }`}
                              >
                                {actionLoading === approval.id ? (
                                  <div className="w-3 h-3 border border-white border-t-transparent rounded-full animate-spin" />
                                ) : (
                                  <>
                                    {approval.middle_manager_status === 'approved' ? '' : '1'} 
                                  </>
                                )}
                              </Button>
                            </div>
                          </div>
                        )
                      })}
                    </div>
                  </div>
                )}
              </CardContent>
            </Card>
          )}
          

          {/*   - Lead Buyer  */}
          {currentUserRoles.includes('lead buyer') && (
            <Card className="w-full col-span-1 border-gray-200 shadow-sm hover:shadow-md transition-shadow">
              <CardHeader className="py-3 px-4 bg-gray-50 border-b">
                <CardTitle className="section-title flex items-center justify-between">
                  <div className="flex items-center gap-2">
                    <ShoppingCart className="w-4 h-4 text-yellow-600" />
                    <span> </span>
                  </div>
                  {data.myPurchaseStatus && data.myPurchaseStatus.waitingPurchase && data.myPurchaseStatus.waitingPurchase.length > 0 && (
                    <Badge className="bg-yellow-100 text-yellow-700 border-yellow-200 px-2 py-0.5">
                      {data.myPurchaseStatus.waitingPurchase.length}
                    </Badge>
                  )}
                </CardTitle>
              </CardHeader>
              <CardContent className="p-4">
                {!data.myPurchaseStatus || !data.myPurchaseStatus.waitingPurchase || data.myPurchaseStatus.waitingPurchase.length === 0 ? (
                  <div className="text-center py-12 text-gray-400">
                    <ShoppingCart className="w-10 h-10 mx-auto mb-3 text-gray-300" />
                    <p className="card-subtitle">   </p>
                  </div>
                ) : (
                  <div className="space-y-3">
                    {/*   */}
                    <div className="relative">
                      <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
                      <Input
                        placeholder=", ,  ..."
                        value={searchTerms.purchase}
                        onChange={(e) => setSearchTerms(prev => ({ ...prev, purchase: e.target.value }))}
                        className="pl-10 h-8 text-xs"
                      />
                    </div>
                    
                    {/*   */}
                    <div className="space-y-2 h-[36rem] overflow-y-auto">
                      {filteredPurchase.slice(0, 10).map((item) => {
                        const items = item.purchase_request_items || []
                        const firstItem = items[0]
                        const totalAmount = items.reduce((sum: number, i: any) => sum + (Number(i.amount_value) || 0), 0)
                        const isSeonJin = (item.progress_type || '').includes('')
                        
                        return (
                          <div 
                            key={item.id} 
                            className={`border rounded-lg p-3 transition-all hover:shadow-sm ${
                              isSeonJin ? 'bg-red-50 hover:bg-red-100 border-red-200' : 'bg-white hover:bg-gray-50 border-gray-200'
                            }`}
                          >
                            <div className="flex items-center justify-between gap-2">
                              <div 
                                className="flex items-center gap-2 flex-1 cursor-pointer"
                                onClick={() => handleStatusClick(item, 'purchase')}
                              >
                                <span className="card-title">
                                  {item.purchase_order_number || `PO-${item.id.slice(0, 8)}`}
                                </span>
                                <span className="card-subtitle truncate">
                                  {item.vendor_name || ' '}
                                </span>
                                <span className="card-description truncate">
                                  {firstItem?.item_name || ''} 
                                  {items.length > 1 && (
                                    <span className="text-gray-400">  {items.length - 1}</span>
                                  )}
                                </span>
                              </div>
                              
                              {/*   - Lead Buyer, App Admin  */}
                              {(currentUserRoles.includes('lead buyer') || 
                                currentUserRoles.includes('app_admin')) && !item.is_payment_completed && (
                                <Button
                                  size="sm"
                                  onClick={async (e) => {
                                    e.stopPropagation()
                                    if (!confirm('   ?')) return
                                    
                                    try {
                                      const { error } = await supabase
                                        .from('purchase_requests')
                                        .update({ 
                                          is_payment_completed: true,
                                          payment_completed_at: new Date().toISOString()
                                        })
                                        .eq('id', item.id)

                                      if (error) throw error
                                      
                                      toast.success(' .')
                                      loadDashboardData(false) //  
                                    } catch (error) {
                                      toast.error('   .')
                                    }
                                  }}
                                  className="bg-yellow-600 hover:bg-yellow-700 text-white h-7 px-2 badge-text shrink-0"
                                >
                                  
                                </Button>
                              )}
                              
                              {/*      */}
                              {item.is_payment_completed && (
                                <div className="bg-green-100 text-green-700 px-2 py-1 business-radius-badge badge-text shrink-0">
                                  
                                </div>
                              )}
                            </div>
                          </div>
                        )
                      })}
                    </div>
                  </div>
                )}
              </CardContent>
            </Card>
          )}

          {/*   */}
          <Card className="w-full col-span-1 border-gray-200 shadow-sm hover:shadow-md transition-shadow">
              <CardHeader className="py-3 px-4 bg-gray-50 border-b">
                <CardTitle className="section-title flex items-center justify-between">
                  <div className="flex items-center gap-2">
                    <Truck className="w-4 h-4 text-blue-600" />
                    <span> </span>
                  </div>
                  {data.myPurchaseStatus.waitingDelivery.length > 0 && (
                    <Badge className="bg-blue-100 text-blue-700 border-blue-200 px-2 py-0.5">
                      {data.myPurchaseStatus.waitingDelivery.length}
                    </Badge>
                  )}
                </CardTitle>
              </CardHeader>
              <CardContent className="p-4">
                {data.myPurchaseStatus.waitingDelivery.length === 0 ? (
                  <div className="text-center py-12 text-gray-400">
                    <Truck className="w-10 h-10 mx-auto mb-3 text-gray-300" />
                    <p className="card-subtitle">   </p>
                  </div>
                ) : (
                  <div className="space-y-3">
                    {/*   */}
                    <div className="relative">
                      <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
                      <Input
                        placeholder=", ,  ..."
                        value={searchTerms.delivery}
                        onChange={(e) => setSearchTerms(prev => ({ ...prev, delivery: e.target.value }))}
                        className="pl-10 h-8 text-xs"
                      />
                    </div>
                    
                    {/*   */}
                    <div className="space-y-2 h-[36rem] overflow-y-auto">
                      {filteredDelivery.slice(0, 10).map((item) => {
                        const items = item.purchase_request_items || []
                        const firstItem = items[0]
                        const totalItems = items.length
                        const receivedItems = items.filter((i: any) => i.is_received).length
                        const progress = totalItems > 0 ? Math.round((receivedItems / totalItems) * 100) : 0
                        const totalAmount = items.reduce((sum: number, i: any) => sum + (Number(i.amount_value) || 0), 0)
                        const isSeonJin = (item.progress_type || '').includes('')
                        
                        return (
                          <div 
                            key={item.id} 
                            className={`border rounded-lg p-3 transition-all cursor-pointer hover:shadow-sm ${
                              isSeonJin ? 'bg-red-50 hover:bg-red-100 border-red-200' : 'bg-white hover:bg-gray-50 border-gray-200'
                            }`}
                            onClick={() => handleStatusClick(item, 'delivery')}
                          >
                            <div className="flex items-center gap-2">
                              <span className="card-title">
                                {item.purchase_order_number || `PO-${item.id.slice(0, 8)}`}
                              </span>
                              <span className="card-subtitle truncate">
                                {item.vendor_name || ' '}
                              </span>
                              <span className="card-description truncate">
                                {firstItem?.item_name || ''} 
                                {totalItems > 1 && (
                                  <span className="text-gray-400">  {totalItems - 1}</span>
                                )}
                              </span>
                            </div>
                          </div>
                        )
                      })}
                    </div>
                  </div>
                )}
              </CardContent>
          </Card>

        </div>

        {/*   -    */}
      </div>
      
      {/*    -     */}
      <LazyPurchaseDetailModal
        purchaseId={selectedApprovalId}
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false)
          setSelectedApprovalId(null)
        }}
        currentUserRoles={currentUserRoles}
        onRefresh={() => {
          loadDashboardData()
          setIsModalOpen(false)
          setSelectedApprovalId(null)
        }}
      />
      
      {/* /    */}
      <PurchaseStatusModal
          isOpen={isStatusModalOpen}
          onClose={() => {
            setIsStatusModalOpen(false)
            setSelectedStatusItem(null)
            setStatusModalType(null)
          }}
          item={selectedStatusItem}
          type={statusModalType as any}
          onRefresh={() => loadDashboardData(false)}
      />

      {/* Order Detail Modal - Apple-inspired Design */}
      {isOrderModalOpen && selectedOrder && (
        <Dialog open={isOrderModalOpen} onOpenChange={() => {
          setIsOrderModalOpen(false)
          setSelectedOrder(null)
        }}>
          <DialogContent 
            className="overflow-hidden bg-white rounded-3xl shadow-2xl border-0"
            style={{ maxWidth: '1280px', width: '90vw', maxHeight: '50vh' }}
            showCloseButton={false}
          >
            {/* Apple-style Header */}
            <div className="relative px-6 pt-6 pb-4">
              <button
                onClick={() => {
                  setIsOrderModalOpen(false)
                  setSelectedOrder(null)
                }}
                className="absolute right-6 top-6 w-8 h-8 rounded-full bg-gray-100 hover:bg-gray-200 flex items-center justify-center transition-all duration-200"
              >
                <X className="w-4 h-4 text-gray-500" />
              </button>
              
              <div className="pr-16">
                <div className="flex items-start gap-4 mb-2">
                  <div className="w-10 h-10 rounded-2xl bg-orange-50 flex items-center justify-center flex-shrink-0">
                    <Download className="w-6 h-6 text-orange-600" />
                  </div>
                  <div className="min-w-0 flex-1">
                    <h1 className="modal-title mb-1">
                      {selectedOrder.purchase_order_number || 'PO '}
                    </h1>
                    <p className="modal-subtitle">{selectedOrder.vendor_name || ' '}</p>
                  </div>
                  <div className={`px-3 py-1.5 business-radius-badge badge-text bg-orange-50 text-orange-700 border-orange-200`}>
                    
                  </div>
                </div>
              </div>
            </div>

            {/* Apple-style Content */}
            <div className="overflow-y-auto max-h-[calc(50vh-160px)] px-6 pb-4 space-y-3">
              
              {/* Dense Basic Information Grid */}
              <div className="bg-gray-50 rounded-xl p-3 border border-gray-100">
                <div className="grid grid-cols-3 gap-x-4 gap-y-2 text-xs">
                  <div><span className="text-gray-500">:</span> <span className="font-medium">{selectedOrder.requester_name}</span></div>
                  <div><span className="text-gray-500">:</span> <span className="font-medium">{new Date(selectedOrder.request_date || selectedOrder.created_at).toLocaleDateString('ko-KR')}</span></div>
                  <div><span className="text-gray-500">:</span> <span className="font-medium">{selectedOrder.delivery_request_date ? new Date(selectedOrder.delivery_request_date).toLocaleDateString('ko-KR') : ''}</span></div>
                  
                  <div><span className="text-gray-500">:</span> <span className="font-medium">{selectedOrder.vendor_name || '-'}</span></div>
                  <div><span className="text-gray-500">:</span> <span className="font-medium">{selectedOrder.payment_category || ''}</span></div>
                  <div><span className="text-gray-500">:</span> <span className="font-medium">{selectedOrder.progress_type || ''}</span></div>
                  
                  <div><span className="text-gray-500">:</span> <span className="font-medium">{selectedOrder.project_vendor || '-'}</span></div>
                  <div><span className="text-gray-500">:</span> <span className="font-medium">{selectedOrder.sales_order_number || '-'}</span></div>
                  <div><span className="text-gray-500">:</span> <span className="font-medium">{selectedOrder.shipping_address || ''}</span></div>
                  
                  <div><span className="text-gray-500">:</span> <span className="font-medium">{selectedOrder.currency || 'KRW'}</span></div>
                  <div><span className="text-gray-500">:</span> <span className="font-medium">{selectedOrder.po_template_type || ''}</span></div>
                  {selectedOrder.revised_delivery_request_date && (
                    <div><span className="text-orange-500">:</span> <span className="font-medium text-orange-900">{new Date(selectedOrder.revised_delivery_request_date).toLocaleDateString('ko-KR')}</span></div>
                  )}
                </div>
              </div>

              {/* Compact Items Table */}
              <div className="bg-white rounded-xl border border-gray-100 overflow-hidden">
                <div className="bg-gray-50 px-3 py-2 border-b border-gray-100">
                  <h3 className="text-sm font-medium text-gray-700">  ({(selectedOrder.purchase_request_items || []).length},  {(selectedOrder.purchase_request_items || []).reduce((sum: number, i: any) => sum + (Number(i.amount_value) || 0), 0).toLocaleString()})</h3>
                </div>
                <div className="overflow-x-auto">
                  <table className="w-full text-xs table-fixed">
                    <colgroup>
                      <col className="w-[30%]" />
                      <col className="w-[25%]" />
                      <col className="w-[10%]" />
                      <col className="w-[15%]" />
                      <col className="w-[20%]" />
                    </colgroup>
                    <thead className="bg-gray-50 border-b border-gray-100">
                      <tr>
                        <th className="text-left p-2 font-medium text-gray-600"></th>
                        <th className="text-left p-2 font-medium text-gray-600"></th>
                        <th className="text-right p-2 font-medium text-gray-600"></th>
                        <th className="text-right p-2 font-medium text-gray-600"></th>
                        <th className="text-right p-2 font-medium text-gray-600"></th>
                      </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-100">
                      {(selectedOrder.purchase_request_items || []).map((pItem: any, index: number) => {
                        const unitPrice = pItem.quantity > 0 ? (Number(pItem.amount_value) || 0) / pItem.quantity : 0
                        return (
                          <tr key={index} className="hover:bg-gray-50">
                            <td className="p-2">
                              <div className="font-medium text-gray-900">{pItem.item_name || ' '}</div>
                              {pItem.remark && (
                                <div className="text-xs text-amber-600 mt-1">: {pItem.remark}</div>
                              )}
                            </td>
                            <td className="p-2 text-gray-600">{pItem.specification || '-'}</td>
                            <td className="p-2 text-right font-medium">{pItem.quantity || 0}</td>
                            <td className="p-2 text-right">{unitPrice.toLocaleString()}</td>
                            <td className="p-2 text-right font-medium">{(Number(pItem.amount_value) || 0).toLocaleString()}</td>
                          </tr>
                        )
                      })}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>

            {/* Apple-style Action Bar */}
            <div className="sticky bottom-0 bg-white border-t border-gray-100 px-6 py-6">
              <div className="flex items-center justify-between gap-6">
                <Button
                  onClick={() => handleDownloadExcel(selectedOrder)}
                  disabled={downloadingIds.has(selectedOrder.id)}
                  className="bg-gradient-to-r from-orange-500 to-orange-600 hover:from-orange-600 hover:to-orange-700 text-white px-8 py-4 rounded-2xl shadow-lg transition-all duration-200 modal-subtitle"
                >
                  {downloadingIds.has(selectedOrder.id) ? (
                    <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin mr-3" />
                  ) : (
                    <Download className="w-5 h-5 mr-3" />
                  )}
                  Excel 
                </Button>

                <div className="flex items-center gap-4 ml-auto">
                  <Button
                    variant="outline"
                    onClick={() => {
                      navigate(`/purchase/list?tab=purchase`)
                      setIsOrderModalOpen(false)
                      setSelectedOrder(null)
                    }}
                    className="border-gray-300 text-gray-700 hover:bg-gray-50 hover:text-gray-900 hover:border-gray-400 px-8 py-4 rounded-2xl modal-subtitle transition-all duration-200"
                  >
                      
                    <ArrowRight className="w-5 h-5 ml-3" />
                  </Button>
                  <Button 
                    onClick={() => {
                      setIsOrderModalOpen(false)
                      setSelectedOrder(null)
                    }} 
                    className="bg-gray-900 hover:bg-gray-800 text-white px-10 py-4 rounded-2xl modal-subtitle transition-all duration-200 shadow-lg"
                  >
                    
                  </Button>
                </div>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      )}
    </div>
  )
}